import React from 'react';
import bubbleOne from "./../assets/images/bubbleOne.png";
import bubbleTwo from "./../assets/images/bubbleTwo.png";
import Chip from '@material-ui/core/Chip';
import "./../assets/styles/About.css";
import { makeStyles } from '@material-ui/core/styles';

// font-size: 18px;
// background-color: #F44336;
// color: white;
// border: #F44336;

const useStyles = makeStyles((theme) => ({
    chip: {
        fontSize: '16px',
        backgroundColor: '#F44336',
        color: 'white',
        margin: 4,
    }
}));

const About = () => {
    const classes = useStyles();
    return(
        <div id="about" className="cnt about">
            <div className="content">
                <div className="cntSubtitle">
                    <h3 className="subTitle">About</h3>
                    <div className="border" />
                </div>
                <p>
                Hi, there! My name is Daniela and I'm a UI Designer and Front End Developer from Córdoba, Argentina. <br />
                I enjoy taking complex problems and turning them into simple and beautiful interface designs. I also love the logic and structure of coding and always strive to write elegant and efficent code. <br />
                I’m constantly learning web technologies. <br />
                <br />
                I’m a skilled in many areas of design and development.<br />
                Some of my skills include:<br />
                </p>
                <div className="skills">
                    <Chip className={classes.chip} style={{backgroundColor: '#ff5722'}} label="HTML" />
                    <Chip className={classes.chip} style={{backgroundColor: '#ff9800'}} label="CSS" />
                    <Chip className={classes.chip} style={{backgroundColor: '#ffc107'}} label="LESS" />
                    <Chip className={classes.chip} style={{backgroundColor: '#cddc39'}} label="JavaScript" />
                    <Chip className={classes.chip} style={{backgroundColor: '#8BC34A'}} label="Responsive Web Design" />
                    <Chip className={classes.chip} style={{backgroundColor: '#4CAF50'}} label="Visual Basic .NET" />
                    <Chip className={classes.chip} style={{backgroundColor: '#009688'}} label="React .js" />
                    <Chip className={classes.chip} style={{backgroundColor: '#3F51B5'}} label="Redux" />
                    <Chip className={classes.chip} style={{backgroundColor: '#9C27B0'}} label="GIT" />
                    <Chip className={classes.chip} style={{backgroundColor: '#E91E63'}} label="Adobe Photoshop / Illustrator / XD" />
                    <Chip className={classes.chip} style={{backgroundColor: '#F44336'}} label="Bootscrap & Material UI frameworks" />
                </div>
            </div>
            <img className="bubble right"src={bubbleOne}/>
            <img className="bubble left" src={bubbleTwo}/>
        </div>
    )
}
;
export default About;