import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import MenuDrawer from './MenuDrawer';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';
import './../assets/styles/Slider.css';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    button: {
        marginRight: '10px',
        color: 'white',
        textTransform: 'initial',
        fontSize: 'large',
    },
        icon: {
        fontSize: 'xx-large',
        color: 'white'
    }
}));



const Header = () => {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [navbar, setNavbar] = useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const changeBackground = () => {
        if (window.scrollY >= 200) {
            setNavbar(true);
        } else {
            setNavbar(false);
        }

        // console.log(window.scrollY)
    }

    window.addEventListener('scroll', changeBackground);

    return (
        <>
            <nav className={navbar ? 'navbar active' : 'navbar'}>
                <div className="cntButtons">
                    <Button className={classes.button} href="#home">Home</Button>
                    <Button className={classes.button} href="#about">About</Button>
                    <Button className={classes.button} href="#proyects">Proyects</Button>
                    <Button className={classes.button} href="#contact">Contact</Button>
                </div>
            </nav>
            <nav style={{display: 'none'}} className={navbar ? 'phoneNavbar active' : 'phoneNavbar'}>
                <div className="cntButtons">
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="end"
                    onClick={handleDrawerOpen}
                    fontSize="medium"

                >
                    <MenuIcon className={classes.icon} />
                </IconButton>
                </div>
            </nav>
                <MenuDrawer open={open} handleDrawerOpen={handleDrawerOpen} handleDrawerClose={handleDrawerClose} />
        </>
    );
}

export default Header;