import React from 'react';
import bubble from "./../assets/images/bubbleFive.png";
import bubbleRight from "./../assets/images/bubbleSix.png";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import './../assets/styles/Contact.css';
import { GitHub, Instagram, LinkedIn } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        '& .MuiTextField-root': {
            marginBottom: 12,
            width: '100%',
            '& label.Mui-focused': {
                color: '#FF9800',
            },
        },
        '& .MuiOutlinedInput-root': {
            '&.Mui-focused fieldset': {
                borderColor: '#FF9800',
            },
        },
    },

    button: {
        width: '100%',
        backgroundColor: '#FF9800',
        textTransform: 'initial',
        color: 'white',
        fontSize: '16px',
        height: 53,
        '&:hover': {
            backgroundColor: '#FF9800',
        }
    },
    icon: {
        color: "#ff5722"
    }
}));

const Contact = () => {
    const classes = useStyles();

    const [value, setValue] = React.useState('');

    const handleChange = (event) => {
        setValue(event.target.value);
    };

    return (
        <div id="contact" className="cnt about contact">
            <div className="content">
                <div className="cntSubtitle">
                    <h3 className="subTitle">Contact</h3>
                    <div className="border" />
                </div>
                <div className="contect formAndMap">
                    <form className={classes.root} action="https://formsubmit.co/danielaliendo98@gmail.com" method="POST" enctype="multipart/form-data">
                        <TextField name="Nombre" type="text" className={classes.inputs} id="outlined-basic" label="Your name..." variant="outlined" required />
                        <TextField name="Email" type="email" className={classes.inputs} id="outlined-basic" label="Your email..." variant="outlined" required />
                        <TextField name="Subjet" type="text" className={classes.inputs} id="outlined-basic" label="Subjet..." variant="outlined" required />
                        <TextField name="Message" type="text"
                            id="outlined-multiline-static"
                            label="Message..."
                            multiline
                            rows={6}
                            variant="outlined"
                            required
                        />
                        <input type="hidden" name="_subject" value="Portfolio - Daniela Liendo" />
                        <input type="hidden" name="_next" value="http://localhost:3000/#home?formSubmit" />
                        <input type="hidden" name="_template" value="box" />
                        <input type="hidden" name="_template" value="table" />
                        <input type="hidden" name="_captcha" value="false" />

                        <Button type="submit" className={classes.button} variant="contained" disableElevation>
                            Send
                        </Button>
                    </form>
                    <div className="cntMap">
                        <iframe className="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d217954.93388626844!2d-64.33443037073714!3d-31.399083974371262!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x9432985f478f5b69%3A0xb0a24f9a5366b092!2zQ8OzcmRvYmE!5e0!3m2!1ses-419!2sar!4v1607804976205!5m2!1ses-419!2sar" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
                        <div className="icons">
                            <div>
                                <IconButton className={classes.icon} target="_blank" href="https://github.com/danielaliendo" color="primary" aria-label="add to shopping cart">
                                    <GitHub />
                                </IconButton>
                                <IconButton className={classes.icon} fontSize="large" target="_blank" href="https://www.linkedin.com/in/daniela-liendo-026289189/" color="primary" aria-label="add to shopping cart">
                                    <LinkedIn />
                                </IconButton>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <img className="bbleLeft" src={bubble} />
            <img className="bbleRight" src={bubbleRight} />
        </div>
    )
}

export default Contact;