import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Chip from '@material-ui/core/Chip';
import chipImg from "./../assets/images/backgroundColors.jpg"

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        [theme.breakpoints.up('md')]: {
            display: 'none'
        },
    },
    drawerPaper: {
        width: drawerWidth,
        width: 300,
        borderRadius: '100% 0 0',
        backgroundImage: `url(${chipImg})`,
        '& .MuiDivider-root': {
            display: 'none'
        }

    },
    drawerHeader: {
        margin: 20,
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-start',
        border: 'none',
    },
    buttonChip: {
        position: 'absolute',
        height: 40,
        borderRadius: 20,
        width: 150,
        marginBottom: 10,
        color: 'white',
        fontSize: '18px',
        fontWeight: '600',
        backgroundColor: 'transparent',

        '&:hover': {
            backgroundColor: '#8E0000'
        },
        '&:focus': {
            backgroundColor: 'transparent',
            boxShadow: 'none'
        }
    },
    drawerButton: {
        position: 'absolute',
        right: 0,
    },
    iconButton: {
        color: 'white'
    },
    chips: {
        marginTop: 100,
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column'
    }
}));

export default function PersistentDrawerRight({ handleDrawerOpen, open, handleDrawerClose }) {
    const classes = useStyles();
    const theme = useTheme();

    return (
        <div className={classes.root}>
            <CssBaseline />
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="right"
                open={open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <IconButton className={classes.drawerButton} onClick={handleDrawerClose}>
                        <ChevronLeftIcon className={classes.iconButton}/>
                    </IconButton>
                </div>
                <Divider />
                <div className={classes.chips}>
                    <Chip style={{top: 0, left: 124}} className={classes.buttonChip} label="Home" component="a" href="#home" clickable onClick={handleDrawerClose} />
                    <Chip style={{top: 62, left: 124}} className={classes.buttonChip} label="About" component="a" href="#about" clickable onClick={handleDrawerClose} />
                    <Chip style={{top: 124, left: 124}} className={classes.buttonChip} label="Proyects" component="a" href="#proyects" clickable onClick={handleDrawerClose} />
                    <Chip style={{top: 186, left: 124}} className={classes.buttonChip} label="Contact" component="a" href="#contact" clickable onClick={handleDrawerClose} />
                </div>
            </Drawer>
        </div>
    );
}