import React from 'react';
import SliderImage from './../assets/images/homeSlider.png';
import phoneImage from './../assets/images/phoneSliderTwo.png';
import "./../assets/styles/Slider.css";

const Home = () => {
    return(
        <div id="home">
            <img className="sliderImg" src={SliderImage}/>
            <img className="phoneImg" style={{}}src={phoneImage}/>
            <div className="description">
                <h1>FRONTEND WEB DEVELOPER</h1>
                <h2>AND CREATIVE UI/UX DESIGNER</h2>
            </div>
        </div>
    )
}

export default Home;