import React from 'react';
import epacNotebook from "./../assets/images/Proyects/EPAC/notebook-01.png";
import epacPhone from "./../assets/images/Proyects/EPAC/phone-01.png";
import bubbleThree from "./../assets/images/bubbleThree.png";
import bubbleFour from "./../assets/images/bubbleFour.png";
import Chip from '@material-ui/core/Chip';
import { makeStyles } from '@material-ui/core/styles';
import './../assets/styles/Proyects.css';

// CHIP CLASS
// position: absolute;
//     bottom: -20px;
//     left: 337px;
//     z-index: 1;
//     background-color: #8E0000;
//     color: white;

const useStyles = makeStyles((theme) => ({
    buttonChip: {
        fontSize: 18,
        position: 'absolute',
        bottom: '-20px',
        left: '323px',
        zIndex: 1,
        backgroundColor: '#8E0000',
        color: 'white',
        '&:hover': {
            backgroundColor: '#8E0000',
        },
        '&:focus': {
            backgroundColor: '#8E0000',
        },
        [theme.breakpoints.down('sm')]: {
            left: 0,
            width: '100%',
            bottom: 0,
            color: 'white',
            zIndex: 1,
            position: 'absolute',
            fontSize: 18,
        },
    }
}));

const Proyects = () => {

    const classes = useStyles();

    return (
        <div id="proyects" className="cnt about">
            <div className="content">
                <div className="cntSubtitle">
                    <h3 className="subTitle">Proyects</h3>
                    <div className="border" />
                </div>
                <div className="proyect">
                    <img className="notebook" src={epacNotebook} />
                    <img className="phone" src={epacPhone} />
                    <Chip className={classes.buttonChip} label="Go to website" component="a" href="https://danielaliendo.github.io/EPAC/" clickable target="_blank" />
                </div>

            </div>
            <img className="bubbleProyect bblLeft" src={bubbleThree} />
            <img className="bubbleProyect bblRight" src={bubbleFour} />
        </div>
    )
}

export default Proyects;